/**
 * WordPress Dependencies
 */
import { render } from '@wordpress/element';
import domReady from '@wordpress/dom-ready';

import ThemesOptoutModal from './themes-optout-modal';
import './style.scss';

const THEMES_OPTOUT_MODAL_ID = 'nextgen-themes-optout-modal';

function initializeThemesOptoutModal() {
	render(
		<ThemesOptoutModal />,
		document.getElementById( THEMES_OPTOUT_MODAL_ID )
	);
}

domReady( () => {
	initializeThemesOptoutModal();
} );
