/**
 * WordPress dependencies
 */
import { Modal } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import * as SVGS from './../../../common/src/svgs';
import { EID_PREFIX } from './../constant';

export default function ThemesOptoutModal() {
	const [ href, setHref ] = useState( null );
	const [ isOpen, setOpen ] = useState( false );

	const closeModal = () => setOpen( false );

	useEffect( () => {
		const clickHandler = ( e ) => {
			if ( ! e.target.classList.contains( 'activate' ) ) {
				return;
			}

			e.preventDefault();

			setOpen( true );
			setHref( e.target.href );
		};

		window.addEventListener( 'click', clickHandler );

		return () => {
			window.removeEventListener( 'click', clickHandler );
		};
	}, [] );

	if ( ! isOpen ) {
		return null;
	}

	const onActivate = () => {
		window.location.href = href;
	};

	return (
		<Modal
			className="nextgen-modal themes-optout-modal"
			onRequestClose={ () => setOpen( false ) }
		>
			<h2 className="nextgen-modal__title">{ __( 'This will change your site editor, too', 'nextgen' ) }</h2>

			<p>{ __( 'You are currently using the GoDaddy Site Editor with the Go theme, which gives you all of WordPress plus a way to customize your design, edit your pages and manage your pages, all from one place.', 'nextgen' ) }</p>

			<div className="nextgen-modal__warning">
				{ SVGS.SVG_WARNING }
				<p>{ __( 'Once you activate a new theme, this site will no longer work with the GoDaddy Site Editor. Your site will switch to the default WordPress Editor instead.', 'nextgen' ) }</p>
			</div>

			<p>{ __( 'Ready to activate a new theme and switch to the default WordPress editor?', 'nextgen' ) }</p>

			<footer className="themes-optout-modal__footer">
				<button
					className="themes-optout-modal__footer__button themes-optout-modal__footer__button--secondary"
					data-eid={ `${ EID_PREFIX }.themes/nextgen_notice.go_back.click` }
					onClick={ closeModal }>
					{ __( 'Go Back', 'nextgen' ) }
				</button>
				<button
					className="themes-optout-modal__footer__button themes-optout-modal__footer__button--primary"
					data-eid={ `${ EID_PREFIX }.themes/nextgen_notice.activate.click` }
					onClick={ onActivate }>
					{ __( 'Activate', 'nextgen' ) }
				</button>
			</footer>
		</Modal>
	);
}
